import {Box, Flex} from '@semcore/ui/flex-box';
import SidePanel from '@semcore/ui/side-panel';
import {List} from '@semcore/ui/typography';

import useSettingsStore, {sideBarTypes} from '../../stores/settingsStore';
import {SidebarListItem} from './SidebarListItem/SidebarListItem';
import Link from '@semcore/ui/link';

import {Text} from '@semcore/ui/typography';
import TwitterM from '@semcore/icon/Twitter/m/index';
import FavoriteFilledM from '@semcore/icon/FavoriteFilled/m/index';
import FacebookM from '@semcore/icon/Facebook/m/index';
import { useEffect, useState } from 'react';

import { sidebarListItems } from './SettingsSidebar';
import React from 'react';

export const SettingsPanelSidebar = () => {
  const [appData, setAppData] = useState({
    rateUrl: '',
    privacyUrl: '',
    termsUrl: ''
  })

  const activeSettingsSection = useSettingsStore(
    (state) => state.activeSettingsSection
  );

  const changeActiveSettingsSection = useSettingsStore(
    (state) => state.setActiveSettingsSection
  );

  const sidePanelVisibility = useSettingsStore(
    (state) => state.sidePanelVisible
  );

  const changePanelVisibility = useSettingsStore(
    (state) => state.changeSidePanelVisibility
  );

  const rateHandler = () => window.open(appData.rateUrl);
  const privacyHandler = () => window.open(appData.privacyUrl);
  const termsHandler = () => window.open(appData.termsUrl);
  const twitterHandler = () => window.open("https://app-cdn.standsapp.org/pages/share-redirect/twitter.html");
  const facebookHandler = () => window.open("https://app-cdn.standsapp.org/pages/share-redirect/fb.html");

  const SidebarListItemOption = (props: { action: any; title: string; }) => {
    const {action, title} = props;
    return (
      <SidebarListItem
        action={() => {
          changeActiveSettingsSection(action);
        }}
        active={activeSettingsSection === action}
        title={title}
      />
    )
  }

  useEffect(() => {
    // @ts-ignore
    appDataService.register(function(data) {
      setAppData(data)
    })
  }, [])

  return (
    <SidePanel
      overlay={false}
      placement="left"
      style={{
        background: '#F4F5F9',
      }}
      visible={sidePanelVisibility}
      onClose={() => {
        changePanelVisibility(false);
      }}
    >
      <SidePanel.Panel
        w={210}
        style={{
          background: '#F4F5F9',
        }}
      >
        <SidePanel.Body p={0}>
          <Flex
            wMin={'100%'}
            hMin={'100%'}
            justifyContent="space-between"
            style={{flexDirection: 'column'}}
          >
            <Box wMin={150} mt={10}>
              <List marker={null} className="reversed-list" w={205}>
                {
                  sidebarListItems.map((item) => (
                    <SidebarListItemOption 
                      action={item.action}
                      title={item.title}
                    />
                  ))
                }
              </List>
            </Box>

            <Flex
              style={{alignSelf: 'flex-end', flexDirection: 'column'}}
              w={200}
              alignItems={'center'}
              mb={'30px'}
            >
              <Flex gap={'12px'}>
                <TwitterM
                  interactive
                  color="var(--gray-main)"
                  width={24}
                  height={24}
                  style={{'--color-interactive_pdrly': 'var(--main-blue)'}}
                  onClick={twitterHandler}
                />
                <FavoriteFilledM
                  interactive
                  color="var(--gray-main)"
                  width={24}
                  height={24}
                  style={{'--color-interactive_pdrly': 'var(--main-blue)'}}
                  onClick={rateHandler}
                />
                <FacebookM
                  interactive
                  color="var(--gray-main)"
                  width={24}
                  height={24}
                  style={{'--color-interactive_pdrly': 'var(--main-blue)'}}
                  onClick={facebookHandler}
                />
              </Flex>

              <Flex mt={4}>
                <Text
                  fontWeight={400}
                  fontSize={'12px'}
                  lineHeight="16px"
                  color="var(--gray-secondary)"
                >
                  {chrome.i18n.getMessage('version')} 2.3
                </Text>
              </Flex>
              <Flex mt={4}>
                <Link color="var(--gray-600)" onClick={privacyHandler}>
                  <Text fontSize={'14px'} lineHeight="18px" fontWeight={400}>
                    {chrome.i18n.getMessage('privacy_policy')}
                  </Text>
                </Link>
              </Flex>
              <Flex mt={4}>
                <Link color="var(--gray-600)" onClick={termsHandler}>
                  <Text fontSize={'14px'} lineHeight="18px" fontWeight={400}>
                    {chrome.i18n.getMessage('terms_of_use')}
                  </Text>
                </Link>
              </Flex>
            </Flex>
          </Flex>
        </SidePanel.Body>
      </SidePanel.Panel>
    </SidePanel>
  );
};
