import {Box, Flex} from '@semcore/ui/flex-box';
import {Text} from '@semcore/ui/typography';
import Input from '@semcore/ui/input';
import Button from '@semcore/ui/button';
import MathPlusM from '@semcore/icon/MathPlus/m/index';
import Link from '@semcore/ui/link';
import {TrashIcon} from '../assets/images';
import React, {useEffect, useState} from 'react';
import { siteRegex } from './PopupAllowedSitesSettings';

export const WhitelistedSitesSettings = () => {
  const [deactivatedSites, setDeactivatedSites] = useState([] as string[]);
  const [currentSite, setCurrentSite] = useState<string>('');
  const [error, setError] = useState<boolean>(false);

  const deleteSite = (index: number) => {
    const host = deactivatedSites[index];
    // @ts-ignore
    appDataService.setHostActivation(host, false, true);
    setCurrentSite('');
  };

  const addSiteToList = () => {
    if (siteRegex.test(currentSite)) {
      const newValue = new URL(currentSite.trim());
      const someValue = newValue.hostname.indexOf('www.') == 0 ? newValue.hostname.substring(4) : newValue.hostname;
    
      // @ts-ignore
      appDataService.setHostActivation(someValue, true, true);
      setCurrentSite('');
      setError(false)
    } else {
      setError(true)
    }
  };

  const changeCurrentSite = (value: string) => {
    if (error) {
      setError(false);
    }
    setCurrentSite(value);
  };

  useEffect(() => {
    // @ts-ignore
    appDataService.register(function(data: {
      deactivatedSites: string[]
    }) {
      const list = data?.deactivatedSites?.filter(site => site !== '');
      setDeactivatedSites(list);
    });
  }, [])

  return (
    <>
      <Box>
        <Text fontSize={'30px'} lineHeight="39px" fontWeight={400}>
          {chrome.i18n.getMessage('whitelisted_sites')}
        </Text>
      </Box>
      <Box mt={3}>
        <Text fontSize={'14px'} lineHeight="18px" color="var(--gray-secondary)">
          {chrome.i18n.getMessage('whitelisted_text')}
        </Text>
        <Box my={6}>
          <Flex>
            <Input size="m" state={error ? 'invalid' : 'normal'} w={212}>
              <Input.Value
                placeholder="https://example.com/"
                onChange={changeCurrentSite}
                value={currentSite}
              />
            </Input>
            <Button size="m" use="secondary" ml={2} onClick={addSiteToList}>
              <Button.Addon>
                <MathPlusM />
              </Button.Addon>
              <Button.Text>
                {chrome.i18n.getMessage('add_site')}
              </Button.Text>
            </Button>
          </Flex>
        </Box>

        <Box>
          <Text bold fontSize={'18px'} lineHeight="23px">
            {chrome.i18n.getMessage('added_sites')}{' '}
            <Text fontWeight={400}>
              ({deactivatedSites.length})
            </Text>
          </Text>

          <Box>
            {deactivatedSites.map((siteName, index) => (
              <Flex mt={3} justifyContent="space-between">
                <Text
                  fontSize={'14px'}
                  lineHeight="20px"
                  color="var(--gray-secondary)"
                >
                  {siteName}
                </Text>
                <Link
                  ml={1}
                  className="delete-icon-wrapper"
                  onClick={() => {
                    deleteSite(index);
                  }}
                >
                  <TrashIcon fill="red" />
                </Link>
              </Flex>
            ))}
          </Box>
        </Box>
      </Box>
    </>
  );
};
