import {Box, Flex} from '@semcore/ui/flex-box';
import {StandsLogo} from '../assets/images';
import {Text} from '@semcore/ui/typography';
import Link from '@semcore/ui/link';
import {SwitchItem} from '../components/SwitchItem.tsx/SwitchItem';
import {DonationSubscriptionOptionsSection} from '../components/DonationSubscriptionOptionsSection/DonationSubscriptionOptionsSection';
import {DonationOptionsSection} from '../components/DonationOptionsSection/DonationOptionsSection';
import Button from '@semcore/ui/button';
import {PayPalButton} from '../components/PayPalButton/PayPalButton';

import {FireFoxIcon, ChromeIcon, EdgeIcon, SafariIcon} from '../assets/images';
import {useContext, useEffect, useState} from 'react';
import Breakpoints from '@semcore/ui/breakpoints';
import {useNavigate} from 'react-router-dom';

import {donations} from '../stores/donations';
import { basicOptionsList, advancedOptionsList } from '../stores/blockingSettings';
import React from 'react';

export const NewOnboardingScreen = () => {
  const [donationPeriod, setDonationPeriod] = useState('Monthly');
  const [userSettings, setUserSettings] = useState<Record<string, any>>({
    blockAds: false,
    blockAdsOnFacebook: false,
    blockAdsOnSearch: false,
    blockMalware: false,
    blockPopups: false,
    blockSponsoredStories: false,
    blockTracking: false,
    blockWebmailAds: false
  });

  const index = useContext(Breakpoints.Context); // 1 - mobile, 0 - desktop
  const [currentAlignment, setCurrentAlignment] = useState<
    'flex-start' | 'center'
  >('center');
  const navigation = useNavigate();

  const toggleSetting = (settingName: string, value: any) => {
    const newSetting = {} as Record<string, any>;
    newSetting[settingName] = value;
    // @ts-ignore
    userSettingsService.update(newSetting)
  }

  useEffect(() => {
    if (index > 0) {
      setCurrentAlignment('flex-start');
    } else {
      setCurrentAlignment('center');
    }

    // @ts-ignore
    userSettingsService.register(function(settings) {
      setUserSettings(settings)
    })
  }, [index]);

  const redirectToSettings = () => {
    navigation('/settings/main', {replace: false});
  };

  const firefoxLinker = () => window.open('https://addons.mozilla.org/ru/firefox/addon/stands-fair-adblocker/');
  const edgeLinker = () => window.open('https://microsoftedge.microsoft.com/addons/detail/fair-adblocker/jccfboncbdccgbgcbhickioeailgpkgb');
  const chromeLinker = () => window.open('https://chrome.google.com/webstore/detail/fair-adblocker/lgblnfidahcdcjddiepkckcfdhpknnjh');

  return (
    <div className="fullscreen-page">
      <Flex
        justifyContent={'center'}
        alignItems={currentAlignment}
        style={{flexDirection: 'column'}}
        px={index === 1 ? '30px' : 0}
      >
        <Box w={'auto'} mb={'20px'} mt={'26px'}>
          <StandsLogo width={201} height={61} />
        </Box>
        <Flex mb={'30px'} w={'100%'} wMax={805} justifyContent="center">
          <Text
            fontSize={'14px'}
            lineHeight={'23px'}
            color="var(--gray-secondary)"
            textAlign={index === 0 ? 'center' : 'start'}
          >
            {chrome.i18n.getMessage('board_text_1')}{' '}
            <Link onClick={redirectToSettings}>
              {chrome.i18n.getMessage('settings').toLowerCase()}
            </Link>
            {' '}{chrome.i18n.getMessage('board_text_2')}
          </Text>
        </Flex>
        <Box wMax={634} w={'100%'}>
          <Flex justifyContent={currentAlignment}>
            <Text fontSize={'22px'} lineHeight={'29px'} fontWeight={700}>
              {chrome.i18n.getMessage('blocking_settings')}
            </Text>
          </Flex>
          <Flex justifyContent={'space-between'} mt={'20px'}>
            <Flex
              style={{flexDirection: 'column'}}
              rowGap={'8px'}
              wMax={275}
              w={'100%'}
              mr={2}
            >
              {basicOptionsList.map((info) => {
                  const {text, toggleSettingName, tooltipText} = info;
                  return (
                    <SwitchItem
                      text={text}
                      tooltipText={tooltipText}
                      value={userSettings[toggleSettingName]}
                      listener={(value:any) => toggleSetting(toggleSettingName, value)}
                    />
              )})}
            </Flex>
            <Flex
              style={{flexDirection: 'column'}}
              rowGap={'8px'}
              wMax={275}
              w={'100%'}
            >
              {advancedOptionsList.map((info) => {
                  const {text, toggleSettingName, tooltipText} = info;
                  return (
                    <SwitchItem
                      text={text}
                      tooltipText={tooltipText}
                      value={userSettings[toggleSettingName]}
                      listener={(value:any) => toggleSetting(toggleSettingName, value)}
                    />
              )})}
            </Flex>
          </Flex>
        </Box>

        <Flex 
          mt={'40px'} 
          style={{flexDirection: 'column', alignItems: 'center'}} 
          w={'100%'} 
          wMax={805}
        >
          <Flex justifyContent={currentAlignment}>
            <Text fontWeight={700} fontSize={'22px'} lineHeight={'29px'}>
              {chrome.i18n.getMessage('donation_settings_title')}
            </Text>
          </Flex>
          <Box
            mt={'10px'}
            style={{textAlign: index === 0 ? 'center' : 'start'}}
          >
            <Text
              fontSize={'14px'}
              lineHeight={'23px'}
              color="var(--gray-secondary)"
              textAlign={index === 0 ? 'center' : 'start'}
            >
              {chrome.i18n.getMessage('donation_settings_text')}
            </Text>
          </Box>
          <Button
            size="l"
            mt='15px'
            theme="warning"
            h="40px"
            use="primary"
            p="0 45px"
            style={{fontSize: '14px', backgroundColor: '#E46863', borderRadius: '15px'}}
            onClick={() => window.open('https://www.standsapp.org/donate/')}
          >
            {chrome.i18n.getMessage('donate')}
          </Button>
        </Flex>

        {/* <Box mt={10} wMax={index === 0 ? 510 : '100%'} w={'100%'}>
          <DonationSubscriptionOptionsSection
            listener={(value: string) => setDonationPeriod(value)}
            shortNames={false}
          />
          <Box mt={5} mb={7}>
            <DonationOptionsSection 
              donationOptionsList={donations[donationPeriod]}
              priceHandler={() => {}} 
            />
          </Box>
          <Flex columnGap={5}>
            <Button
              size="l"
              theme="danger"
              height="50px"
              use="primary"
              style={{width: '100%', marginBottom: 6}}
              disabled={true}
            >
              Pay with credit card
            </Button>

            <PayPalButton clickAction={() => {}} disabled={donationSum <= 0}/>
          </Flex>
        </Box> */}

        <Box mt={'40px'} mb={'26px'}>
          <Flex mb={'20px'} justifyContent={currentAlignment}>
            <Text bold fontSize={'18px'} lineHeight={'23px'}>
              {chrome.i18n.getMessage('available_on')}
            </Text>
          </Flex>
          <Flex gap={5}>
            <FireFoxIcon style={{cursor: 'pointer'}} onClick={firefoxLinker}/>
            <EdgeIcon style={{cursor: 'pointer'}} onClick={edgeLinker}/>
            {/* <SafariIcon /> */}
            <ChromeIcon style={{cursor: 'pointer'}} onClick={chromeLinker}/>
          </Flex>
        </Box>
      </Flex>
    </div>
  );
};
