import {Box, Flex} from '@semcore/ui/flex-box';

import ChevronLeftL from '@semcore/icon/ChevronLeft/l';
import {Text} from '@semcore/ui/typography';
import {SwitchItem} from '../components/SwitchItem.tsx/SwitchItem';
import Input from '@semcore/ui/input';
import {useEffect, useState} from 'react';
import Textarea from '@semcore/ui/textarea';
import Button from '@semcore/ui/button';
import {useNavigate} from 'react-router-dom';
import {ButtonStates} from './FeedbackScreen';
import {Navbar} from '../components/Navbar/Navbar';

import { emailRegex } from './FeedbackScreen';
import React from 'react';

export const ReportIssueScreen = () => {
  const navigation = useNavigate();

  const [reportMessage, setReportMessage] = useState('');
  const [includeURL, setIncludeURL] = useState(true);
  const [userEmail, setUserEmail] = useState<string>('');

  const [emailButtonState, setEmailButtonState] = useState<
    keyof typeof ButtonStates
  >(ButtonStates.normal);

  const [userData, setUserData] = useState({
    email: ''
  });

  const handleEmailChange = (value: string) => {
    setUserEmail(value);

    if (value === '') {
      setEmailButtonState(ButtonStates.invalid);
    } else if (emailButtonState === 'invalid') {
      setEmailButtonState(ButtonStates.normal);
    } else if (emailButtonState === 'normal' && emailRegex.test(value)) {
      setEmailButtonState(ButtonStates.valid);
    } else if (emailButtonState === 'valid' && !emailRegex.test(value)) {
      setEmailButtonState(ButtonStates.normal);
    }
  };

  const handleReportURLInclude = (include: boolean) => {
    setIncludeURL(include);
  }

  const handleReportDescriptionChange = (value: string) => {
    setReportMessage(value);
  };

  const handleSetReport = () => {
    if (includeURL || reportMessage) {
      // @ts-ignore
      messaging.post('report-issue', {
          includeCurrentUrlInReport: includeURL,
          email: userEmail || userData.email,
          feedback: reportMessage
      });
    }

    redirectToMainScreen();
  }

  const redirectToMainScreen = () => {
    navigation('/', {replace: true});
  };

  useEffect(() => {
    // @ts-ignore
    userDataService.register(function(data){
      setUserData(data.email)
    })
  }, [])

  return (
    <Box
      p={5}
      className="App"
      style={{
        borderRadius: 12,
      }}
    >
      <Navbar />
      <Flex onClick={redirectToMainScreen} mt={5} style={{cursor: 'pointer'}}>
        <Box mr={'10px'}>
          <ChevronLeftL interactive />
        </Box>
        <Text fontSize={'16px'} lineHeight="24px" bold>
          {chrome.i18n.getMessage('report_an_issue')}
        </Text>
      </Flex>
      <Box mt={'18px'}>
        <Text color="var(--gray-2)" fontSize={'14px'} lineHeight="18px">
          {chrome.i18n.getMessage('report_issue_text')}
        </Text>
      </Box>
      <Box my={'14px'}>
        <SwitchItem 
          text={chrome.i18n.getMessage('include_current_url')}
          value={includeURL}
          listener={handleReportURLInclude}
        />
      </Box>
      {emailButtonState === 'invalid' && (
        <Box mb={'14px'}>
          <Text color="var(--light-red)" fontSize={'14px'} lineHeight={'18px'}>
            {chrome.i18n.getMessage('email_cant_be_empty')}
          </Text>
        </Box>
      )}
      <Box>
        <Input size="l" state={emailButtonState}>
          <Input.Value
            placeholder="Email"
            value={userEmail}
            onChange={handleEmailChange}
          />
        </Input>
      </Box>
      <Box mt={'10px'}>
        <Textarea
          value={reportMessage}
          onChange={handleReportDescriptionChange}
          maxRows={5}
          minRows={5}
          placeholder={chrome.i18n.getMessage('describe_the_issue')}
          resize="none"
          size="l"
        />
      </Box>
      <Box mt={'14px'}>
        <Button
          size="l"
          theme="info"
          use="primary"
          className="feedbackButton"
          style={{width: '100%', color: 'white'}}
          onClick={handleSetReport}
          disabled={emailButtonState !== 'valid' || !reportMessage}
        >
          {chrome.i18n.getMessage('send')}
        </Button>
      </Box>
    </Box>
  );
};
