import {Box, Flex} from '@semcore/ui/flex-box';
import {Text} from '@semcore/ui/typography';
import TabLine from '@semcore/ui/tab-line';
import {GraphStatisticsInfoCard} from '../components/GraphStatisticsInfoCard/GraphStatisticsInfoCard';
import {useState, useEffect} from 'react';
import {StatisticsGraph} from '../components/StatisticsGraph/StatisticsGraph';
import { CardInfoProps } from '../types/entities';
import { cardInfo } from '../stores/statisticsCardInfo';
import React from 'react';

export const BlockingStatistics = () => {
  const [activeCardIndex, setActiveCardIndex] = useState<number>(0);
  const [activeTimeSpanIndex, setActiveTimeSpanIndex] = useState<number>(0);
  const [blockingData, setBlockingData] = useState<Record<string, any>>({
    blocks: 0,
    adServers: 0,
    trackers: 0,
    malware: 0,
    popups: 0
  });

  useEffect(() => {
    // @ts-ignore
    blockingDataService.register(function(data) {
      setBlockingData(data);
    });
  },[])

  const StatisticsCard = (props: CardInfoProps): JSX.Element => {
    const {number, numberColor, title, cardIndex} = props;
    return (
    <Box 
      onClick={() => {
        setActiveCardIndex(cardIndex)
      }} 
      mr={3} 
      w={'20%'}
    >
      <GraphStatisticsInfoCard
        number={blockingData[number]}
        numberColor={numberColor}
        title={title}
        active={activeCardIndex === cardIndex}
      />
    </Box>
  )}

  return (
    <>
      <Box>
        <Text fontSize={'30px'} lineHeight="39px" fontWeight={400}>
          {chrome.i18n.getMessage('blocking_statistic')}
        </Text>
      </Box>
      <Box mt={3}>
        <Text fontSize={'14px'} lineHeight="18px" color="var(--gray-secondary)">
          {chrome.i18n.getMessage('blocking_statistics_text')}
        </Text>
        <Flex mt={'26px'} style={{flexDirection: 'column'}}>
          {/* <TabLine
            size="m"
            underlined={false}
            style={{
              alignSelf: 'flex-end',
              width: 'auto',
              color: 'var(--red-300)',
            }}
            value={activeTimeSpanIndex}
            onChange={setActiveTimeSpanIndex}
          >
            <TabLine.Item value={0}>Daily</TabLine.Item>
            <TabLine.Item value={1}>Weekly</TabLine.Item>
            <TabLine.Item value={2}>Monthly</TabLine.Item>
            <TabLine.Item value={3}>All Time</TabLine.Item>
          </TabLine> */}

          <Flex mt={'30px'} hMin={108}>
            {
              cardInfo.map((item) => 
                <StatisticsCard {...item}/>
              )
            }
          </Flex>

          <Box>
            <StatisticsGraph options={cardInfo}/>
          </Box>
        </Flex>
      </Box>
    </>
  );
};
