import {Box, Flex} from '@semcore/ui/flex-box';

import ChevronLeftL from '@semcore/icon/ChevronLeft/l';
import {Text} from '@semcore/ui/typography';
import Input from '@semcore/ui/input';
import {useEffect, useState} from 'react';
import Textarea from '@semcore/ui/textarea';
import Button from '@semcore/ui/button';
import {useNavigate} from 'react-router-dom';
import {Navbar} from '../components/Navbar/Navbar';
import React from 'react';

export enum ButtonStates {
  normal = 'normal',
  invalid = 'invalid',
  valid = 'valid',
}

export const emailRegex = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,4})+$/;

export const FeedbackScreen = () => {
  const navigation = useNavigate();

  const [userEmail, setUserEmail] = useState<string>('');
  const [feedback, setFeedback] = useState<string>('');
  const [userSettings, setUserSettings] = useState({
    geo: ''
  });

  const [emailButtonState, setEmailButtonState] = useState<
    keyof typeof ButtonStates
  >(ButtonStates.normal);

  const handleEmailChange = (value: string) => {
    setUserEmail(value);

    if (value === '') {
      setEmailButtonState(ButtonStates.invalid);
    } else if (emailButtonState === 'invalid') {
      setEmailButtonState(ButtonStates.normal);
    } else if (emailButtonState === 'normal' && emailRegex.test(value)) {
      setEmailButtonState(ButtonStates.valid);
    } else if (emailButtonState === 'valid' && !emailRegex.test(value)) {
      setEmailButtonState(ButtonStates.normal);
    }
  };

  const handleFeedbackChange = (value: string) => {
    setFeedback(value);
  };

  const redirectToMainScreen = () => {
    navigation('/', {replace: true});
  };

  const handleSendClick = () => {
    const content = 'From: ' + userEmail + '\nGeo: ' + userSettings.geo + '\nFeedback: ' + feedback;
    // @ts-ignore
    sendEmail('Feedback', 'App', content)

    navigation('/', {replace: true});
  };

  useEffect(() => {
    // @ts-ignore
    userSettingsService.register(function(settings){
      setUserSettings(settings.geo)
    })
  }, [])
  
  return (
    <Box
      p={5}
      className="App"
      style={{
        borderRadius: 12,
      }}
    >
      <Navbar />
      <Flex onClick={redirectToMainScreen} mt={5} style={{cursor: 'pointer'}}>
        <Box mr={'10px'}>
          <ChevronLeftL interactive />
        </Box>
        <Text fontSize={'16px'} lineHeight="24px" bold>
          {chrome.i18n.getMessage('feedback')}
        </Text>
      </Flex>
      <Box mt={'18px'} mb={'14px'}>
        <Text color="var(--gray-2)" fontSize={'14px'} lineHeight="18px">
          {chrome.i18n.getMessage('feedback_screen_text')}
        </Text>
      </Box>
      {emailButtonState === 'invalid' && (
        <Box mb={'14px'}>
          <Text color="var(--light-red)" fontSize={'14px'} lineHeight={'18px'}>
            {chrome.i18n.getMessage('email_cant_be_empty')}
          </Text>
        </Box>
      )}
      <Box>
        <Input size="l" state={emailButtonState}>
          <Input.Value
            placeholder={chrome.i18n.getMessage('email')}
            value={userEmail}
            onChange={handleEmailChange}
          />
        </Input>
      </Box>
      <Box mt={'10px'}>
        <Textarea
          value={feedback}
          onChange={handleFeedbackChange}
          maxRows={5}
          minRows={5}
          placeholder={chrome.i18n.getMessage('feedback')}
          resize="none"
          size="l"
        />
      </Box>
      <Box mt={'14px'}>
        <Button
          size="l"
          theme="info"
          use="primary"
          className="feedbackButton"
          style={{width: '100%', color: 'white'}}
          onClick={handleSendClick}
          disabled={!feedback}
        >
          {chrome.i18n.getMessage('send')}
        </Button>
      </Box>
    </Box>
  );
};
