import Button from '@semcore/ui/button';
import {Flex} from '@semcore/ui/flex-box';
import React from 'react';
import {useState} from 'react';

import { donationPeriods } from '../../stores/donations';

interface Props {
  listener: any,
  shortNames: boolean
}

export const DonationSubscriptionOptionsSection: React.FC<Props> = ({
  listener,
  shortNames
}) => {
  const [activeOptionIndex, setActiveOptionIndex] = useState<number>(0);

  const changeActiveSubscriptionOption = (optionIndex: number) => {
    setActiveOptionIndex(optionIndex);
  };

  const standartDonationPeriods = [
    'Monthly',
    'Yearly'
  ]

  return (
    <Flex
      className="donation-selector"
      justifyContent={'flex-start'}
      columnGap={5}
    >
      {standartDonationPeriods.map((name, index) => {
        const shortName = donationPeriods[name].short;
        const fullName = donationPeriods[name].full;
        return (
          <Button
            active={activeOptionIndex === index}
            use="tertiary"
            onClick={() => {
              changeActiveSubscriptionOption(index);
              listener(name)
            }}
          >
            {shortNames ? shortName : fullName}
          </Button>
        );
      })}
    </Flex>
  );
};
