import {Flex} from '@semcore/ui/flex-box';
import {DonationOptionItem} from '../DonationOptionItem/DonationOptionItem';
import {useState} from 'react';
import { DonationOptionItemProps } from '../../types/entities';
import React from 'react';

export const DonationOptionsSection = (props: {
  donationOptionsList: DonationOptionItemProps[],
  priceHandler: any,
  // customSumHandler: any
}) => {
  const {donationOptionsList, priceHandler} = props;
  const [activeCardIndex, setActiveCardIndex] = useState<number>(0);

  const changeActiveCard = (cardIndex: number, cardPrice: string) => {
    setActiveCardIndex(cardIndex);
    priceHandler(cardPrice);
  };

  return (
    <Flex mt={'8px'} style={{flexDirection: 'column', alignItems: 'center'}}>
      {donationOptionsList.map((item) => {
        const {price, description, image, index, noMarginBottom} = item;
        return (
          <div
            style={{
              marginBottom: noMarginBottom ? 0 : 8,
              width: '100%', 
              cursor: 'pointer'
            }}
            onClick={() => changeActiveCard(index, price)}
          >
            <DonationOptionItem
              price={price}
              description={description}
              image={image}
              active={activeCardIndex === index}
            />
          </div>
        )
      })}
    </Flex>
  );
};
