import {Flex} from '@semcore/ui/flex-box';
import {Text} from '@semcore/ui/typography';

import {useEffect, useState} from 'react';
import React from 'react';

export const DebugPage = () => {
  const [userSettings, setUserSettings] = useState('');
  const [adBlockerData, setAdBlockerData] = useState('');
  const [userData, setUserData] = useState('');
  const [dashboardData, setDashboardData] = useState('');
  const [appData, setAppData] = useState('');
  const [blockingData, setBlockingData] = useState('');
  const [blockedElementsData, setBlockedElementsData] = useState('');
  const [pagesData, setPagesData] = useState({});
  const [ruleMatches, setRuleMatches] = useState({});
  const [logs, setLogs] = useState({});

  const [selectedData, setSelectedData] = useState('userSettings');
  const [selectedIndex, setSelectedIndex] = useState('');
  const [selectedDate, setSelectedDate] = useState('');
  const [selectedLog, setSelectedLog] = useState('');

  const staticData = {
    "userSettings": userSettings,
    "adBlockerData": adBlockerData,
    "userData": userData,
    "dashboardData": dashboardData,
    "appData": appData,
    "blockingData": blockingData,
    "blockedElementsData": blockedElementsData
  }

  function dataPerformer(data) {
    let text = JSON.stringify(data);
    text = text.substring(1, text.length-1);
    text = text.replace(/,"/g,',\n"');
    text = text.replace(/\[{/g,'\n\[{\n');
    text = text.replace(/\["/g,'\n\[\n"');
    text = text.replace(/"\]/g,'"\n\]');
    text = text.replace(/{"/g,'\n{\n"');
    text = text.replace(/[0-9]}}/,'_\n}\n}');
    text = text.replace(/[0-9]}/g,'_\n}');
    text = text.replace(/}}/g,'\n}\n}');
    text = text.replace(/}\]/g,'\n}\]');
    text = text.replace(/"}/g,'"\n}');
    text = text.replace(/true}/g,'true\n}');
    text = text.replace(/:/g,': ');
    return text
  }

  useEffect(() => {
    // @ts-ignore
    userSettingsService.register(function(data) {
      setUserSettings(data)
    })
    // @ts-ignore
    adBlockerDataService.register(function(data) {
      setAdBlockerData(data)
    })
    // @ts-ignore
    userDataService.register(function(data) {
      setUserData(data)
    })
    // @ts-ignore
    dashboardDataService.register(function(data) {
      setDashboardData(data)
    })
    // @ts-ignore
    appDataService.register(function(data) {
      setAppData(data)
    })
    // @ts-ignore
    blockingDataService.register(function(data) {
      setBlockingData(data)
    })
    // @ts-ignore
    blockedElementsDataService.register(function(data) {
      setBlockedElementsData(data)
    })
    // @ts-ignore
    pagesDataService.register(function(data) {
      const index = Object.keys(data)[0];
      setSelectedIndex(index)
      setPagesData(data)
    })
    // @ts-ignore
    ruleMatchesService.register(function(data) {
      const index = Object.keys(data)[0];
      setSelectedDate(index)
      setRuleMatches(data)
    })

    chrome.storage.local.get((data) => {
      const logIndex = Object.keys(data)[0];
      setSelectedLog(logIndex)
      setLogs(data)
    })
  }, []);

  const infoComponentOptions = [
    {
      title: 'Data for popup',
      value: selectedData,
      data: staticData,
      valueSetter: setSelectedData
    },
    {
      title: 'Rule matches',
      value: selectedDate,
      data: ruleMatches,
      valueSetter: setSelectedDate
    },
    {
      title: 'chrome.storage.local',
      value: selectedLog,
      data: logs,
      valueSetter: setSelectedLog
    },
    {
      title: 'Pages data',
      value: selectedIndex,
      data: pagesData,
      valueSetter: setSelectedIndex
    }
  ]

  const InfoComponentContainer = () => {
    return (
      <Flex>
        {
          infoComponentOptions.map(option => (
            <InfoComponent
              title={option.title}
              value={option.value}
              data={option.data}
              valueSetter={option.valueSetter}
            />
          ))
        }
      </Flex>
    )
  }

  const InfoComponent = (props: {
    title, value, valueSetter, data
  }) => {
    const {title, value, valueSetter, data} = props;

    return (
      <Flex 
        style={{
          flexDirection: 'column', 
          alignItems: 'center',
          height: '100vh',
          width: '100%',
          overflow: 'auto',
          borderLeft: '1px solid black',
          borderRight: '1px solid black',
          padding: '0 1vw'
        }}
      >
        <h3>
          {title}
        </h3>
        
        <select value={value} onChange={(event) => valueSetter(event.target.value)}>
          {
            Object.keys(data).map(item => (
              <option value={item}>
                {item}
              </option>
            ))
          }
        </select>
        <Flex 
          mt={'10px'} 
          style={{
            flexDirection: 'column', 
            alignItems: 'left',
            overflowWrap: 'anywhere'
          }}
        >
          <Text style={{whiteSpace: 'pre-wrap', fontSize: '12px'}}>
            {
              data[value] ?
                dataPerformer(data[value]) :
                'nothing'
            }
          </Text>
        </Flex>
      </Flex>
  )}

  return (
    <div className="fullscreen-page" style={{margin: '0 auto'}}>
      <InfoComponentContainer/>      
    </div>
  );
};
