import {Flex} from '@semcore/ui/flex-box';
import Link from '@semcore/ui/link';
import Tooltip from '@semcore/ui/tooltip';
import {useNavigate} from 'react-router-dom';
import {
  HealthRectangleIcon,
  HearthIcon,
  SliderIcon,
  StandsLogo,
} from '../../assets/images';
import useSettingsStore, {sideBarTypes} from '../../stores/settingsStore';
import ArrowLeftM from '@semcore/icon/ArrowLeft/m';
import React from 'react';

interface Props {
  backArrow?: boolean;
}

export const Navbar: React.FC<Props> = ({backArrow}) => {
  const navigation = useNavigate();
  const changeActiveSettingsSection = useSettingsStore(
    (state) => state.setActiveSettingsSection
  );

  const redirectToMainScreen = () => {
    navigation('/', {replace: true});
  };

  const openSettingsScreen = () => {
    changeActiveSettingsSection(sideBarTypes.statistics);
    window.open('#/settings/statistics');
  };
  const openMainSettingsScreen = () => {
    window.open('#/settings/main');
  };

  const openDonationScreen = () => {
    // navigation('/donation', {replace: true});
    window.open('https://www.standsapp.org/donate/');
  };
  
  return (
    <Flex justifyContent={'space-between'}>
      <div onClick={redirectToMainScreen} style={{cursor: 'pointer'}}>
        <StandsLogo height={30} width={100} />
      </div>
      <Flex>
        {backArrow && (
          <Tooltip
            interaction={'hover'}
            title={chrome.i18n.getMessage('back_to_home')}
            style={{display: 'flex', alignItems: 'center'}}
          >
            <Link
              className="icon-wrapper"
              onClick={redirectToMainScreen}
              w={30}
            >
              <ArrowLeftM interactive height={'22px'} width={'22px'} />
            </Link>
          </Tooltip>
        )}
        <Flex w={105} justifyContent="space-between"> 
          <Tooltip 
            interaction="hover" 
            title={chrome.i18n.getMessage('donate')}
          >
            <Link className="icon-wrapper" onClick={openDonationScreen}>
              <HearthIcon />{' '}
            </Link>
          </Tooltip>
          <Tooltip 
            interaction="hover" 
            title={chrome.i18n.getMessage('statistic')}
          >
            <Link className="icon-wrapper" onClick={openSettingsScreen}>
              <HealthRectangleIcon />
            </Link>{' '}
          </Tooltip>
          <Tooltip 
            interaction="hover" 
            title={chrome.i18n.getMessage('settings')}
          >
            {' '}
            <Link className="icon-wrapper" onClick={openMainSettingsScreen}>
              <SliderIcon />
            </Link>
          </Tooltip>
        </Flex>
      </Flex>
    </Flex>
  );
};
