import {Box, Flex} from '@semcore/ui/flex-box';
import {Text} from '@semcore/ui/typography';
import {SwitchItem} from '../components/SwitchItem.tsx/SwitchItem';
import Slider from '@semcore/ui/slider';
import {useState, useEffect} from 'react';
import {
  FaceIcon_0, 
  FaceIcon_1, 
  FaceIcon_2_3, 
  FaceIcon_4, 
  FaceIcon_5_6
} from '../assets/images';
import React from 'react';

export const FairnessSettings = () => {
  const [fairAds, setFairAds] = useState<boolean>(false);
  const [maxAdsAmount, setMaxAdsAmount] = useState<number>(1);

  const faceIcons = [
    <FaceIcon_0 />,
    <FaceIcon_1 />,
    <FaceIcon_2_3 />,
    <FaceIcon_2_3 />,
    <FaceIcon_4 />,
    <FaceIcon_5_6 />,
    <FaceIcon_5_6 />
  ]

  const handleAdsAmountChange = (value: string | number) => {
    const newAmount = Number(value);

    if (newAmount === 0) {
      // @ts-ignore
      userSettingsService.update({adsEnabled: false})
    }

    if (maxAdsAmount === 0 && newAmount > 0) {
      // @ts-ignore
      userSettingsService.update({adsEnabled: true})
    }
    // @ts-ignore
    userSettingsService.update({maxAdsPerPage: newAmount})
  };

  const handleFairAdsChange = (value: boolean) => {
    if (maxAdsAmount === 0 && value === true) {
      // @ts-ignore
      userSettingsService.update({maxAdsPerPage: 3})
    }
    // @ts-ignore
    userSettingsService.update({adsEnabled: value})
  }

  useEffect(() => {
    // @ts-ignore
    userSettingsService.register(function(settings) {
      setFairAds(settings.adsEnabled);
      setMaxAdsAmount(settings.maxAdsPerPage);
    })
  }, [])

  return (
    <>
      <Box>
        <Text fontSize={'30px'} lineHeight="39px" fontWeight={400}>
          {chrome.i18n.getMessage('fairness_settings')}
        </Text>
      </Box>
      <Box mt={3}>
        <SwitchItem
          isTextBig
          text={chrome.i18n.getMessage('allow_fair_ads')}
          value={fairAds}
          listener={handleFairAdsChange}
          descriptionText={chrome.i18n.getMessage('allow_fair_ads_description')}
        />
      </Box>
      <Box mt={3}>
        <Text bold fontSize={'18px'} lineHeight="23px">
          {chrome.i18n.getMessage('fairness_settings_text')}
        </Text>

        <Box mt={3}>
          <Flex>
            <Slider
              className="custom-slider"
              w={276}
              style={{
                color: 'var(--gray-secondary)',
                fontSize: 12,
                lineHeight: '16px',
              }}
              disabled={!fairAds && maxAdsAmount !== 0}
              value={maxAdsAmount}
              onChange={handleAdsAmountChange}
              step={1}
              min={0}
              max={6}
              options={Array.from({length: 7})
                .fill(0)
                .map((_, index) => ({value: index, label: index}))}
            />
            <Box ml={'30px'}>
              {
                faceIcons[maxAdsAmount]
              }
            </Box>
          </Flex>
        </Box>
      </Box>
    </>
  );
};
