import {Box, Flex} from '@semcore/ui/flex-box';
import {Text} from '@semcore/ui/typography';
import Link from '@semcore/ui/link';
import Button from '@semcore/ui/button';
import MathPlusM from '@semcore/icon/MathPlus/m/index';

import {TrashIcon} from '../assets/images';
import {SwitchItem} from '../components/SwitchItem.tsx/SwitchItem';
import {SmallDataCardItem} from '../components/SmallDataCardItem/SmallDataCardItem';

import TwitterM from '@semcore/icon/Twitter/m/index';
import FavoriteFilledM from '@semcore/icon/FavoriteFilled/m/index';
import FacebookM from '@semcore/icon/Facebook/m/index';
import {useNavigate} from 'react-router-dom';
import {useEffect, useState} from 'react';
import { Navbar } from '../components/Navbar/Navbar';
import { NoticeCard } from '../components/NoticeCard/NoticeCard';
import React from 'react';

export const MainScreen = () => {
  const [sitename, setSitename] = useState<string | undefined>('');
  const [validSite, setValidSite] = useState(true);
  const [appData, setAppData] = useState({
    currentHostSettings: {
      site: '',
      isDeactivated: false,
      blockPopups: true
    },
    currentPageData: {
      blocks: 0,
      adServersBlocks: 0,
      adwareBlocks: 0,
      popupBlocks: 0,
      trackersBlocks: 0
    },
    blocking: {
      total: {
        adServersBlocks: 0,
        adwareBlocks: 0,
        popupBlocks: 0,
        trackersBlocks: 0
      }
    },
    blocksCounter: 0,
    rateUrl: ''
  });
  const [blockedElementsCount, setBlockedElementsCount] = useState(0);
  const navigation = useNavigate();

  const getSitename = async () => {
    let queryOptions = {
      active: true,
      lastFocusedWindow: true
    };
    chrome.tabs.query(queryOptions, (tabs) => {
      let url = tabs[0].url;
      if (url?.substring(0, url.indexOf('://')) === 'chrome-extension') {
        setSitename('');
        return
      }
      url = url?.substring(url.indexOf('://') + 3);
      url = url?.substring(0, url.indexOf('/'));
      setSitename(url);
    })
  };

  const blockElements = function() {
    // @ts-ignore
    messaging.post('block-element');
    window.close();
  };

  const undoBlockedElements = function() {
    // @ts-ignore
    blockedElementsDataService.unblockElements()
    window.close();
  }

  const toggleCurrentHostActivation = function() {
    const host = appData.currentHostSettings.site;
    const deactivate = !appData.currentHostSettings.isDeactivated;
    // @ts-ignore
    appDataService.setHostActivation(host, deactivate, true);
    window.close();
  };

  const toggleCurrentHostPopupBlocking = function() {
    const blockPopups = appData.currentHostSettings.blockPopups;
    const host = appData.currentHostSettings.site;
    // @ts-ignore
    appDataService.setHostPopupBlocking(host, blockPopups, true);
    window.close();
  };

  useEffect(() => {
    getSitename();

    // @ts-ignore
    appDataService.register(function(data) {
      if (!data.currentHostSettings && !data.currentPageData) {
        setValidSite(false)
      } else {
        setValidSite(true)
      }

      setAppData({
        currentHostSettings: 
          data.currentHostSettings ? 
          data.currentHostSettings :
          {
            site: '',
            isDeactivated: false,
            blockPopups: true
          },
        currentPageData: 
          data.currentPageData ? 
          data.currentPageData :
          {
            blocks: 0,
            adServersBlocks: 0,
            adwareBlocks: 0,
            popupBlocks: 0,
            trackersBlocks: 0
          },
        blocking: data.blocking,
        blocksCounter: data.blocksCounter,
        rateUrl: data.rateUrl
      });
    });

    // @ts-ignore
    blockedElementsDataService.register(function(count) {
      setBlockedElementsCount(count);
    });
  }, []);

  const smallDataCardItemSiteValues = {
    adServers: appData.currentPageData.adServersBlocks,
    malware: appData.currentPageData.adwareBlocks,
    popups: appData.currentPageData.popupBlocks,
    trackers: appData.currentPageData.trackersBlocks
  }

  const smallDataCardItemTotalValues = {
    adServers: appData.blocking.total.adServersBlocks,
    malware: appData.blocking.total.adwareBlocks,
    popups: appData.blocking.total.popupBlocks,
    trackers: appData.blocking.total.trackersBlocks
  }
  
  const rateHandler = () => window.open(appData.rateUrl);
  const reportHandler = () => navigation('/report');
  const feedbackHandler = () => navigation('/feedback');
  const twitterHandler = () => window.open("https://app-cdn.standsapp.org/pages/share-redirect/twitter.html");
  const facebookHandler = () => window.open("https://app-cdn.standsapp.org/pages/share-redirect/fb.html");

  return (
    <Box
      p={5}
      className="App"
      style={{
        borderRadius: 12,
      }}
    >
      <Navbar />

      <NoticeCard />

      <Box mt={'18px'}>
        <Text mb={'14px'} bold style={{fontSize: 16, lineHeight: '24px', color: !validSite && 'red'}}>
          {
            validSite ?
              sitename :
              'This is not a valid site'
          }
        </Text>
        <Box my={'10px'}>
          <SwitchItem 
            text={chrome.i18n.getMessage('whitelist_this_site')}
            value={appData.currentHostSettings.isDeactivated}
            listener={toggleCurrentHostActivation}
          />
        </Box>

        <SwitchItem 
          text={chrome.i18n.getMessage('block_popups')}
          value={appData.currentHostSettings.blockPopups}
          listener={toggleCurrentHostPopupBlocking}
        />
      </Box>
      <Flex mt={'18px'} justifyContent="space-between" alignItems="center">
        <Button use="secondary" onClick={blockElements}>
          <Button.Addon>
            <MathPlusM interactive />
          </Button.Addon>
          <Button.Text>
            {chrome.i18n.getMessage('block_element')}
          </Button.Text>
        </Button>
        <Flex alignItems="center">
          <Text
            style={{
              fontSize: 14,
              lineHeight: '20px',
              color: 'var(--gray-secondary)',
            }}
          >
            {chrome.i18n.getMessage('elements_blocked', blockedElementsCount.toString())}
          </Text>
          <Link ml={1} className="delete-icon-wrapper">
            <TrashIcon fill="red" onClick={undoBlockedElements}/>
          </Link>
        </Flex>
      </Flex>
      <Box mt={6}>
        <Text bold style={{fontSize: 16, lineHeight: '24px'}}>
          {chrome.i18n.getMessage('items_blocked')}
        </Text>

        <Flex mt={4}>
          <SmallDataCardItem
            adsAmount={appData.currentPageData.blocks}
            textDescription={chrome.i18n.getMessage('on_this_page')}
            timeSavedNumber={Math.floor(appData.currentPageData.blocks*2)/10}
            timeSavedText={chrome.i18n.getMessage('seconds_saved')}
            total={false}
            details={smallDataCardItemSiteValues}
          />
          <Box wMin={'12px'}></Box>
          <SmallDataCardItem
            adsAmount={appData.blocksCounter}
            textDescription={chrome.i18n.getMessage('in_total')}
            timeSavedNumber={Math.floor(appData.blocksCounter/300)}
            timeSavedText={chrome.i18n.getMessage('minutes_saved')}
            total={true}
            details={smallDataCardItemTotalValues}
          />
        </Flex>
      </Box>
      <Flex mt={6} justifyContent="space-between" className="footer">
        <Flex gap={'8px'}>
          <TwitterM
            interactive
            color="var(--gray-main)"
            width={24}
            height={24}
            style={{'--color-interactive_pdrly': 'var(--main-blue)'}}
            onClick={twitterHandler}
          />
          <FavoriteFilledM
            interactive
            color="var(--gray-main)"
            width={24}
            height={24}
            style={{'--color-interactive_pdrly': 'var(--main-blue)'}}
            onClick={rateHandler}
          />
          <FacebookM
            interactive
            color="var(--gray-main)"
            width={24}
            height={24}
            style={{'--color-interactive_pdrly': 'var(--main-blue)'}}
            onClick={facebookHandler}
          />
        </Flex>
        <Button use="tertiary" theme="info" onClick={feedbackHandler}>
          {chrome.i18n.getMessage('send_feedback')}
        </Button>
        <Button use="tertiary" theme="info" onClick={reportHandler}>
          {chrome.i18n.getMessage('report_an_issue')}
        </Button>
      </Flex>
    </Box>
  );
};
