import {Box, Flex} from '@semcore/ui/flex-box';
import {StandsLogo} from '../assets/images';
import {Text} from '@semcore/ui/typography';
import {CheckboxItem} from '../components/CheckboxItem/CheckboxItem';
import Button from '@semcore/ui/button';
import { useEffect, useState } from 'react';
import Input from '@semcore/ui/input';
import Textarea from '@semcore/ui/textarea';
import { ButtonStates, emailRegex } from './FeedbackScreen';
import React from 'react';

export const UninstallScreen = () => {
  const [country, setCountry] = useState('');
  const [userData, setUserData] = useState({
    privateUserId: '',
    publicUserId: ''
  })
  const [chosenIssue, setChosenIssue] = useState('');
  const [userEmail, setUserEmail] = useState<string>('');
  const [feedback, setFeedback] = useState<string>('');
  const [emailButtonState, setEmailButtonState] = useState<
    keyof typeof ButtonStates
  >(ButtonStates.normal);

  const getCountry = () => {
    const xhttp = new XMLHttpRequest();
    xhttp.onreadystatechange = function() {
        if (this.readyState == 4 && this.status == 200) {
          console.log(JSON.parse(xhttp.response).countryName)
          setCountry(JSON.parse(xhttp.response).countryName);
        }
    };
    xhttp.open("GET", "https://app.standsapp.org/geolookup", true);
    xhttp.send();
  }

  const handleEmailChange = (value: string) => {
    setUserEmail(value);

    if (value === '') {
      setEmailButtonState(ButtonStates.invalid);
    } else if (emailButtonState === 'invalid') {
      setEmailButtonState(ButtonStates.normal);
    } else if (emailButtonState === 'normal' && emailRegex.test(value)) {
      setEmailButtonState(ButtonStates.valid);
    } else if (emailButtonState === 'valid' && !emailRegex.test(value)) {
      setEmailButtonState(ButtonStates.normal);
    }
  };

  const handleFeedbackChange = (value: string) => {
    setFeedback(value);
  };

  const handleSendClick = () => {
    const uninstallEmailText =
      'Email: ' + userEmail +
      '\nCountry: ' + country +
      '\nUninstalled Reason: ' + chosenIssue +
      '\nUser Feedback: ' + feedback +
      '\nPrivate User ID:' + userData.privateUserId +
      '\nPublic User ID:' + userData.publicUserId;
    // @ts-ignore
    sendEmail('Uninstall Feedback', 'Uninstall Feedback Window', uninstallEmailText);

    chrome.tabs.query({active: true}, (tabs) => {
      chrome.tabs.remove(tabs[0].id!)
    })
  };  

  const reasons: Record<string, string> = {
    "I didn't understand what Stands is": "We aim to find the middle ground between ad blockers and what funds the free web - ads - by empowering you to choose to see a few ads that fund the websites you visit and a cause you support.",
    "I had issues with popups": "Oh, we're really sorry for that.\nPlease tell us more and we'll fix it.",
    "I had issues on sites I visit": "Oh, we're really sorry for that.\nWe do our best to give you the best product and experience but the web is big and sometimes we make mistakes.",
    "I still see ads": "If you've seen ads that are not marked as served by Stands then please let us know where so we can fix it.",
    "Other": "We do our best to give you the best product and experience, if you can help us understand what we can do to get better we'll be grateful :)"
  };

  useEffect(() => {
    getCountry();

    // @ts-ignore
    userDataService.register(function(data){
      setUserData({
        privateUserId: data.privateUserId,
        publicUserId: data.publicUserId
      })
    })
}, [])

  return (
    <div className="fullscreen-page">
      <Flex
        justifyContent={'center'}
        alignItems={'center'}
        style={{flexDirection: 'column'}}
      >
        <Box w={'auto'} mt={'90px'}>
          <StandsLogo width={171} height={51} />
        </Box>

        <Box mt={13} pl={2}>
          <Text bold fontSize={'30px'} lineHeight={'39.06px'}>
            {
              !chosenIssue ? 
                'We are sorry to see you go.' :
                '"' + chosenIssue + '"'
            }  
          </Text>
        </Box>
        <Box my={'30px'} wMax="700px" style={{textAlign: 'center'}}>
          <Text
            color="var(--gray-secondary)"
            fontSize={'18px'}
            lineHeight="23.44px"
            textAlign="center"
          >
            {
              !chosenIssue ? 
              'If you could take a moment to let us know why you have chosen to uninstall our product, it would help us improve for future users. Thank you for your time.' :
              reasons[chosenIssue]
            }            
          </Text>
        </Box>

        <Box pl={2}>
          {
            !chosenIssue ?
              Object.getOwnPropertyNames(reasons).map((text) => (
                <Box my={3}>
                  <CheckboxItem 
                    text={text} 
                    handler={() => setChosenIssue(text)}
                  />
                </Box>
              )) :
              <Box wMin='400px'>
                {emailButtonState === 'invalid' && (
                  <Box mb={'14px'}>
                    <Text color="var(--light-red)" fontSize={'14px'} lineHeight={'18px'}>
                      Email cant be empty
                    </Text>
                  </Box>
                )}
                <Box>
                  <Input size="l" state={emailButtonState}>
                    <Input.Value
                      placeholder={'Email (if you want us respond)'}
                      value={userEmail}
                      onChange={handleEmailChange}
                    />
                  </Input>
                </Box>
                <Box mt={'10px'}>
                  <Textarea
                    value={feedback}
                    onChange={handleFeedbackChange}
                    maxRows={5}
                    minRows={5}
                    placeholder={'Feedback (if you want to tell us more)'}
                    resize="none"
                    size="l"
                  />
                </Box>
              </Box>
          }
        </Box>
        {
          chosenIssue &&
          <Flex mt={'50px'} w="100%" justifyContent={'center'}>
            <Button
              size="l"
              theme="danger"
              use="primary"
              h={'50px'}
              style={{width: '100%', maxWidth: '260px'}}
              onClick={handleSendClick}
            >
              Send
            </Button>
          </Flex>
        }
      </Flex>
    </div>
  );
};
