import {Box, Flex} from '@semcore/ui/flex-box';
import Radio, {RadioGroup} from '@semcore/ui/radio';
import {Text} from '@semcore/ui/typography';
import {useEffect, useState} from 'react';
import {StandsIcon} from '../assets/images';
import Counter from '@semcore/ui/counter';
import Skeleton from '@semcore/ui/skeleton';
import React from 'react';

const badgeThemes = {
  2: 'Today',
  1: 'Page',
  0: 'Disabled'
}

const themesBadge = {
  'Today': 2,
  'Page': 1,
  'Disabled': 0
}

export const ThemesSettingsScreen = () => {
  const [badgeTheme, setBadgeTheme] = useState('');
  const [pageBlocks, setPageBlocks] = useState(0);
  const [todayBlocks, setTodayBlocks] = useState(0);

  const badgeOptions = [
    {
      value: 1,
      my: false,
      text: chrome.i18n.getMessage('themes_badge_page')
    },
    {
      value: 2,
      my: true,
      text: chrome.i18n.getMessage('themes_badge_today')
    },
    {
      value: 0,
      my: false,
      text: chrome.i18n.getMessage('themes_badge_disabled')
    }
  ]

  const BadgeOption = ({value, my, text}) => (
    <Radio mr={2} my={my ? 3 : 0}>
      <Radio.Value value={value} />
      <Radio.Text>
        {text}
      </Radio.Text>
    </Radio>
  )

  const badgeThemeHandler = function(value) {
    // @ts-ignore
    userSettingsService.update({iconBadgePeriod: badgeThemes[value]});

    setBadgeTheme(badgeThemes[value])
  };

  useEffect(() => {
    // @ts-ignore
    appDataService.register(function(data) {
      if (data?.currentPageData) {
        setPageBlocks(data.currentPageData.blocks)
        setTodayBlocks(data.blocksToday)
      }
    })

    // @ts-ignore
    userSettingsService.register(function(data) {
      setBadgeTheme(data.iconBadgePeriod);
    });
  }, [])

  return (
    <>
      <Box>
        <Text fontSize={'30px'} lineHeight="39px" fontWeight={400}>
          {chrome.i18n.getMessage('themes_title')}
        </Text>
      </Box>
      <Box my={5}>
        <Text bold fontSize={'18px'} lineHeight={'23px'}>
          {chrome.i18n.getMessage('themes_badge_settings')}
        </Text>
        <Box mt={6}>
          <Flex>
            <Box style={{position: 'relative'}}>
              <StandsIcon
                width={63}
                height={67}
                style={{position: 'relative', bottom: 0}}
              />
              {badgeTheme !== badgeThemes[0] && (
                <Counter
                  size="xl"
                  theme="light-blue"
                  style={{
                    position: 'absolute',
                    bottom: 5,
                    right: 0,
                    fontSize: 18,
                    transform: 'scale(1.2)',
                  }}
                >
                  {badgeTheme === badgeThemes[2] ? todayBlocks : pageBlocks}
                </Counter>
              )}
            </Box>
            <Flex 
              ml={'42px'} 
              style={{
                flexDirection: 'column', 
                justifyContent: 'center'
              }}
            >
              <RadioGroup
                name="radio"
                value={themesBadge[badgeTheme]}
                onChange={(v) => badgeThemeHandler(v)}
              >
                {
                  badgeOptions.map(({value, my, text}) => (
                    <BadgeOption 
                      value={value}
                      my={my}
                      text={text}
                    />
                  ))
                }
              </RadioGroup>
            </Flex>
          </Flex>
        </Box>
      </Box>
    </>
  );
};
