import Card from '@semcore/ui/card';
import CloseM from '@semcore/icon/Close/m';
import {useEffect, useState} from 'react';
import {Flex} from '@semcore/ui/flex-box';
import {Text} from '@semcore/ui/typography';
import Button from '@semcore/ui/button';
import React from 'react';

export const NoticeCard = () => {
  const [isCardVisible, setIsCardVisible] = useState<boolean>(true);

  const openMainSettingsScreen = () => {
    window.open('#/settings/main');
  };

  const showTopBarPage = () => {
    window.open('#/navbar');
  };

  const openUninstallPage = () => {
    window.open('#/uninstall');
  };

  const openMiniPopup = () => {
    chrome.tabs.query({active: true}, (tabs) => {
      chrome.tabs.sendMessage(tabs[0].id!, {
        type: 'stndz-show-popup-notification'
      })
    })
  }

  useEffect(() => {
    // @ts-ignore
    userSettingsService.register(function(settings) {
      setIsCardVisible(settings.guidedSetup)
    })
  }, [])

  return (
    <Card
        mt={'10px'}
        className="dark-card"
        style={!isCardVisible ? {display: 'none'} : {}}
      >
        <CloseM
          onClick={() => setIsCardVisible(false)}
          style={{float: 'right'}}
          height={16}
          width={16}
          interactive
          color="white"
          p={3}
        />

        <Card.Body>
          <Flex>
            <Text style={{fontSize: 14, lineHeight: '20px'}} color="white">
              {chrome.i18n.getMessage('guided_setup')}
            </Text>
          </Flex>

          <Flex mt={2}>
            <Button size="m" theme="invert" mr={2} onClick={openMainSettingsScreen}>
              {chrome.i18n.getMessage('guided_setup_go')}
            </Button>
            <Button size="m" theme="invert" onClick={() => setIsCardVisible(false)}>
              {chrome.i18n.getMessage('guided_setup_later')}
            </Button>
          </Flex>
        </Card.Body>
      </Card>
  );
};
