import {Box} from '@semcore/ui/flex-box';

import {Text} from '@semcore/ui/typography';
import Button from '@semcore/ui/button';
import {DonationOptionsSection} from '../components/DonationOptionsSection/DonationOptionsSection';
import {DonationSubscriptionOptionsSection} from '../components/DonationSubscriptionOptionsSection/DonationSubscriptionOptionsSection';
import {PayPalButton} from '../components/PayPalButton/PayPalButton';
import { useState } from 'react';

import {donations} from '../stores/donations';
import { Navbar } from '../components/Navbar/Navbar';
import React from 'react';

export const DonationScreen = () => {
  const [donationPeriod, setDonationPeriod] = useState('Monthly');
  const [donationSum, setDonationSum] = useState(0);

  const priceHandler = (price: string) => {   
    let sum = Number(price.substring(1));
    setPaypalSum(sum);
    setDonationSum(sum);
  }

  function setPaypalPeriod(val: string) {
    setDonationPeriod(val);
    // @ts-ignore
    return document.getElementById('period-input').value = val.substring(0,1);
  }

  function setPaypalSum(val: number) {
    // @ts-ignore
    document.getElementById('single-amount').value = val;
    // @ts-ignore
    document.getElementById('sum-input').value = val;
  }
  
  return (
    <Box
      p={5}
      className="App"
      style={{
        borderRadius: 12,
      }}
    >
      <Navbar backArrow={true}/>
      <Box mb={'8px'}>
        <Text bold style={{fontSize: 16, lineHeight: '24px'}} mb={'8px'}>
          {chrome.i18n.getMessage('donate')}
        </Text>
        <Box>
          <Text color="#6C6E79" style={{fontSize: 12, lineHeight: '16px'}}>
            {chrome.i18n.getMessage('donation_screen_text')}
          </Text>
        </Box>
      </Box>
      <form className="donation-form" id="donate" target="_blank" action="https://www.sandbox.paypal.com/cgi-bin/webscr" method="post">
        <input type="hidden" id="input-click-action" name="cmd" value="_xclick-subscriptions"/>
        <input type="hidden" name="business" value="payment@standsapp.org"/>
        <input type="hidden" name="lc" value="EN_US"/>
        <input type="hidden" name="currency_code" value="USD"/>
        <input type="hidden" name="no_shipping" value="1"/>
        <input type="hidden" name="no_note" value="1"/>
        <input type="hidden" name="custom" value=""/>
        <input type="hidden" name="return" value=""/>
        <input type="hidden" name="amount" id="single-amount" value="0"/>
        <input type="hidden" name="bn" value="stands_donation"/>
        <input type="hidden" name="cancel_return" value=""/>
        <input type="hidden" name="item_name" value="StandsApp Donation"/>
        <input type="hidden" name="item_number" value=""/>
        <input type="hidden" name="notify_url" value="https://standsapp.org/index.php?wpeppsub-listener=IPN"/>
        <input type="hidden" id="sum-input" name="a3" value="0"/>
        <input type="hidden" name="p3" value="1"/>
        <input type="hidden" id="period-input" name="t3" value="1"/>
        <input type="hidden" name="src" value="1"/>
        <input type="hidden" className="chosed-plan" name="plan" value=""/>
        {/* <input type="hidden" id="stripeToken" name="stripeToken" /> */}
        {/* <input type="hidden" id="stripeEmail" name="stripeEmail" /> */}
        {/* <input type="hidden" id="isCustom" name="custom_price" value="false"/> */}
        <DonationSubscriptionOptionsSection
          listener={(value: string) => setPaypalPeriod(value)}
          shortNames={true}
        />
        <Box mb={'8px'}>
          <DonationOptionsSection 
            donationOptionsList={donations[donationPeriod]} 
            priceHandler={priceHandler}
          />
        </Box>
        <Box>
          {/* <Button
            size="l"
            theme="danger"
            use="primary"
            style={{width: '100%', marginBottom: 6}}
          >
            {chrome.i18n.getMessage('pay_with_credit_card')}
          </Button> */}
          <PayPalButton clickAction={() => {}} disabled={donationSum <= 0}/>
        </Box>
        </form>
    </Box>
  );
};
