import Button from '@semcore/ui/button';
import React from 'react';
import {PayPalLogoIcon} from '../../assets/images';

interface Props {
  clickAction: () => void;
  disabled: boolean
}
export const PayPalButton: React.FC<Props> = ({clickAction, disabled}) => {
  return (
    <Button
      size="l"
      theme="info"
      use="primary"
      className="paypalButton"
      style={{width: '100%', color: 'black'}}
      onClick={clickAction}
      type="submit"
      disabled={disabled}
    >
      <Button.Text>
        {chrome.i18n.getMessage('pay_with')}
      </Button.Text>
      <Button.Addon>
        <PayPalLogoIcon />
      </Button.Addon>
    </Button>
  );
};
