import {Box, Flex} from '@semcore/ui/flex-box';
import Switch from '@semcore/ui/switch';
import {Text} from '@semcore/ui/typography';
import CheckM from '@semcore/icon/Check/m/index';
import CloseM from '@semcore/icon/Close/m/index';
import InfoM from '@semcore/icon/Info/m';
import Tooltip from '@semcore/ui/tooltip';

import { SwitchItemProps } from '../../types/entities';
import React from 'react';

export const SwitchItem: React.FC<SwitchItemProps> = ({
  text,
  tooltipText,
  descriptionText,
  isTextBig,
  listener,
  value
}) => {
  return (
    <Box>
      <Flex justifyContent={'space-between'}>
        <Flex>
          <Text
            style={
              isTextBig
                ? {
                    fontWeight: 700,
                    fontSize: 18,
                    lineHeight: '24px',
                    color: 'var(--gray-700)',
                  }
                : {
                    fontSize: 14,
                    lineHeight: '20px',
                    color: 'var(--gray-secondary)',
                  }
            }
          >
            {text}
          </Text>
          {tooltipText && (
            <Tooltip title={tooltipText} placement="right">
              <InfoM interactive color="var(--gray-300)" />
            </Tooltip>
          )}
        </Flex>

        <Switch size="l" theme="success">
          <Switch.Value onChange={listener} checked={value}>
            {value ? (
              <CheckM height={11} width={11} />
            ) : (
              <CloseM height={11} width={11} />
            )}
          </Switch.Value>
        </Switch>
      </Flex>
      {descriptionText && (
        <Box mt={3}>
          <Text
            fontSize={'16px'}
            lineHeight="21px"
            color="var(--gray-secondary)"
          >
            {descriptionText}
          </Text>
        </Box>
      )}
    </Box>
  );
};
