import {
  Plot,
  Tooltip,
  XAxis,
  YAxis,
  HoverRect,
  StackBar,
  ResponsiveContainer,
} from '@semcore/ui/d3-chart';
import {Flex} from '@semcore/ui/flex-box';
import {scaleBand, scaleLinear} from 'd3-scale';
import {Text} from '@semcore/ui/typography';
import moment from 'moment';
import {useEffect, useState} from 'react';
import { CardInfoProps } from '../../types/entities';
import React from 'react';

const thisMoment = moment({});

export const StatisticsGraph = (props: {
  options: CardInfoProps[]
}) => {
  let options = [...props.options];
  for (let i=0; i<options.length; i++) {
    if (options[i].number === 'blocks') {
      options.splice(i,1);
      break
    }
  }

  const [blockingData, setBlockingData] = useState<Record<string, any>[]>([]);

  const [[width, height], updateSize] = useState([600, 300]);

  const setYScaleDomainMax = () => {
    let max = 0;

    if (blockingData.length) {
      blockingData.forEach((item) => {
        if (item.blocks > max) {
          max = item.blocks;
        }
      })
    }

    return max
  }

  const MARGIN = 40;
  const xScale = scaleBand()
    .range([MARGIN, width - MARGIN])
    .domain(blockingData.map((d) => d.date))
    .paddingInner(0.4)
    .paddingOuter(0.2);
  const yScale = scaleLinear()
    .range([height - MARGIN, MARGIN])
    .domain([0, setYScaleDomainMax()]);

  useEffect(() => {
    // @ts-ignore
    blockingDataService.register(function(data) {
      const formatData: Record<string, any>[] = [];
      const days = Object.getOwnPropertyNames(data.days)
        .sort((a,b) => new Date(a).getTime() - new Date(b).getTime());
      
      for (let i = 0; i < days.length; i++) {
        const item = data.days[days[i]];
        const date = new Date(days[i]).toString().substring(4, 10);
        formatData.push({
          date,
          ...item
        })
      }
      setBlockingData(formatData);
    });
  },[])

  return (
    <ResponsiveContainer h={300} onResize={updateSize}>
      <Plot 
        data={blockingData} 
        scale={[xScale, yScale]} 
        width={width} 
        height={height}
      >
        <YAxis>
          <YAxis.Ticks />
          <YAxis.Grid />
        </YAxis>
        <XAxis>
          <XAxis.Ticks />
        </XAxis>
        <Tooltip tag={HoverRect} x="date" wMin={150}>
          {({xIndex}) => {
            return {
              children: (
                <>
                  <Tooltip.Title>
                    {`${blockingData[xIndex as number].date}, ${thisMoment.year()}`}
                  </Tooltip.Title>
                  {
                    options.map((item) => {
                      const {mt, numberColor, number, title} = item;
                      return (
                        <Flex mt={mt} justifyContent="space-between">
                          <Tooltip.Dot mr={4} color={numberColor}>
                            {title}
                          </Tooltip.Dot>
                          <Text bold>
                            {blockingData[xIndex as number][number]}
                          </Text>
                        </Flex>
                    )})
                  }
                </>
              ),
            };
          }}
        </Tooltip>
        <StackBar x="date">
          {
            options.map((item) => (
              <StackBar.Bar y={item.number} color={item.numberColor} />
            ))
          }
        </StackBar>
      </Plot>
    </ResponsiveContainer>
  );
};
