import {Box, Flex} from '@semcore/ui/flex-box';
import {Text} from '@semcore/ui/typography';
import React, {ReactElement} from 'react';

interface Props {
  price: string;
  description?: string;
  image?: ReactElement<any, any>;
  active: boolean;
}

export const DonationOptionItem: React.FC<Props> = ({
  price,
  description,
  image,
  active,
}) => {
  return (
    <Box
      className={active ? 'donation-card-active': "donation-card"}
      style={
        active
          ? {
              outline: '2px solid #2BB3FF',
              borderRadius: 8,
              width: '100%',
            }
          : {
              background: '#F3F4F5',
              outline: '1px solid #E0E1E9',
              borderRadius: 8,
              width: '100%',
            }
      }
    >
      <Flex
        justifyContent={'space-between'}
        alignItems="center"
        px={'10px'}
        py={'3px'}
        hMin={60}
      >
        <Flex
          alignItems={'flex-start'}
          style={{flexDirection: 'column'}}
          hMin={'100%'}
        >
          <Text bold style={{fontSize: 16, lineHeight: '24px'}}>
            {price}
          </Text>

          {description && (
            <Text
              style={{
                fontSize: 12,
                lineHeight: '16px',
                color: 'var(--grey-secondary)',
              }}
            >
              {description}
            </Text>
          )}
        </Flex>
        <Box hMin={'100%'}>{image}</Box>
      </Flex>
    </Box>
  );
};
