import Card from '@semcore/ui/card';
import {Box, Flex} from '@semcore/ui/flex-box';
import {Text} from '@semcore/ui/typography';
import {
  Plot,
  YAxis,
  XAxis,
  StackBar,
  colors,
  HoverRect,
  Tooltip,
} from '@semcore/ui/d3-chart';

import {scaleLinear, scaleBand} from 'd3-scale';
import React from 'react';

interface Props {
  adsAmount: number | string;
  textDescription: string;
  timeSavedNumber: number;
  timeSavedText: string;
  total: boolean,
  details: {
    adServers: number,
    trackers: number,
    malware: number,
    popups: number
  }
}

export const SmallDataCardItem: React.FC<Props> = ({
  adsAmount,
  textDescription,
  timeSavedText,
  timeSavedNumber,
  details
}) => {
  const adsNumber = Number(adsAmount);
  const data = [{...details, category: '0'}];

  const renderStackedGraph = () => {
    const MARGIN = 5;
    const width = 130;
    const height = 24;

    const xScale = scaleLinear().range([0, width]).domain([0, adsNumber]);
    const yScale = scaleBand()
      .range([height - MARGIN, MARGIN])
      .domain(data.map((d) => d.category))
      .paddingInner(0.4)
      .paddingOuter(0.2);
    
    return (
      <Plot data={data} scale={[xScale, yScale]} width={width} height={height}>
        <YAxis hide={true}></YAxis>
        <XAxis hide={true}></XAxis>
        <Tooltip tag={HoverRect} y="category" wMin={100}>
          {({yIndex, xIndex}) => {
            return {
              children: (
                <>
                  <Text>
                    {chrome.i18n.getMessage('more_info_on_statistics')}
                  </Text>
                </>
              ),
            };
          }}
        </Tooltip>
        <StackBar y='category'>
          <StackBar.HorizontalBar x="adServers" color={colors['var(--blue-500)']}/>
          <StackBar.HorizontalBar x="trackers" color={colors['var(--blue-300)']} />
          <StackBar.HorizontalBar x="malware" color={colors['var(--red-300)']} />  
          <StackBar.HorizontalBar x="popups" color={colors['#A9ABB6']} />
        </StackBar>
      </Plot>
    );
  };

  return (
    <Card wMin={'46%'}>
      <Card.Body>
        <Text bold style={{fontSize: 26, lineHeight: '28px'}}>
          {adsAmount}
        </Text>
        <Box mb={1}>
          <Text
            style={{
              fontSize: 14,
              lineHeight: '20px',
              color: 'var(--gray-secondary)',
            }}
          >
            {textDescription}
          </Text>
        </Box>
        <Box>{renderStackedGraph()}</Box>
        <Flex mt={1} justifyContent="flex-end">
          <Text
            style={{
              fontSize: 12,
              lineHeight: '14px',
              color: 'var(--gray-secondary)',
            }}
          >
            <b>{timeSavedNumber}</b> {timeSavedText}
          </Text>
        </Flex>
      </Card.Body>
    </Card>
  );
};
