import {List} from '@semcore/ui/typography';
import React from 'react';
import {RightArrowIcon} from '../../../assets/images';

interface Props {
  action: () => void;
  title: string;
  active: boolean;
}

export const SidebarListItem: React.FC<Props> = ({action, title, active}) => {
  return (
    <List.Item
      onClick={action}
      className={active && 'active'}
      marker={active ? <RightArrowIcon /> : null}
    >
      {title}
    </List.Item>
  );
};
