import Breakpoints from '@semcore/ui/breakpoints';
import React from 'react';
import {useEffect} from 'react';
import {useParams} from 'react-router-dom';
import useSettingsStore, {sideBarTypes} from '../stores/settingsStore';
import {SettingsScreen} from './SettingsScreen';

export const SettingsBreakpoints = () => {
  const params = useParams();
  const changeActiveSettingsSection = useSettingsStore(
    (state) => state.setActiveSettingsSection
  );

  useEffect(() => {
    const param = params?.specificRoute;
    if (param === 'statistics') {
      changeActiveSettingsSection(sideBarTypes.statistics);
    }
  });
  return (
    <Breakpoints>
      <SettingsScreen />
    </Breakpoints>
  );
};
