import {Box, Flex} from '@semcore/ui/flex-box';
import {Col} from '@semcore/ui/grid';
import {List} from '@semcore/ui/typography';
import {StandsLogo} from '../../assets/images';
import {Text} from '@semcore/ui/typography';

import TwitterM from '@semcore/icon/Twitter/m/index';
import FavoriteFilledM from '@semcore/icon/FavoriteFilled/m/index';
import FacebookM from '@semcore/icon/Facebook/m/index';
import Link from '@semcore/ui/link';
import useSettingsStore, {sideBarTypes} from '../../stores/settingsStore';
import {SidebarListItem} from './SidebarListItem/SidebarListItem';
import {useContext, useEffect, useState} from 'react';
import Breakpoints from '@semcore/ui/breakpoints';
import React from 'react';

export const sidebarListItems = [
  {
    action: sideBarTypes.blockingSettings,
    title: chrome.i18n.getMessage('blocking_settings')
  },
  {
    action: sideBarTypes.fairnessSettings,
    title: chrome.i18n.getMessage('fairness_settings')
  },
  // {
  //   action: sideBarTypes.donate,
  //   title: chrome.i18n.getMessage('donate')
  // },
  {
    action: sideBarTypes.statistics,
    title: chrome.i18n.getMessage('statistic')
  },
  {
    action: sideBarTypes.whitelistedSites,
    title: chrome.i18n.getMessage('whitelisted_sites')
  },
  {
    action: sideBarTypes.popupAllowedSites,
    title: chrome.i18n.getMessage('popup_allowed_sites')
  },
  {
    action: sideBarTypes.themes,
    title: chrome.i18n.getMessage('themes')
  }
]

export const SettingsSidebar = () => {
  const [appData, setAppData] = useState({
    rateUrl: '',
    privacyUrl: '',
    termsUrl: ''
  })

  const index = useContext(Breakpoints.Context);
  const [
    activeSettingsSection,
    changeActiveSettingsSection
  ] = useSettingsStore((state) => [
    state.activeSettingsSection,
    state.setActiveSettingsSection,
    state.changeSidePanelVisibility,
  ]);

  const rateHandler = () => window.open(appData.rateUrl);
  const privacyHandler = () => window.open(appData.privacyUrl);
  const termsHandler = () => window.open(appData.termsUrl);
  const twitterHandler = () => window.open("https://app-cdn.standsapp.org/pages/share-redirect/twitter.html");
  const facebookHandler = () => window.open("https://app-cdn.standsapp.org/pages/share-redirect/fb.html");

  const SidebarListItemOption = (props: { action: any; title: string; }) => {
    const {action, title} = props;
    return (
      <SidebarListItem
        action={() => {
          changeActiveSettingsSection(action);
        }}
        active={activeSettingsSection === action}
        title={title}
      />
    )
  }

  useEffect(() => {
    // @ts-ignore
    appDataService.register(function(data) {
      setAppData(data)
    })
  }, [])

  return (
    <Col
      span={4}
      style={{
        background: '#F4F5F9',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
      }}
    >
      <Box>
        <Flex wMin={'100%'} justifyContent="flex-start">
          {/* <Flex wMax={'100%'} w={'20%'}></Flex> */}
          <StandsLogo
            style={{
              minHeight: 38,
              height: 'auto',
              width: '100%',
              maxWidth: 168,
              minWidth: 127,
              marginLeft: '20%',
              marginTop: 20,
              marginBottom: 20,
            }}
          />
        </Flex>
        <Flex justifyContent={'flex-end'}>
          <Box wMin={150}>
            <List marker={null} className="reversed-list" w={205}>
              {
                sidebarListItems.map((item) => (
                  <SidebarListItemOption 
                    action={item.action}
                    title={item.title}
                  />
                ))
              }
            </List>
          </Box>
        </Flex>
      </Box>

      <Flex
        style={{alignSelf: 'flex-end', flexDirection: 'column'}}
        w={200}
        alignItems={'center'}
        mb={'30px'}
      >
        <Flex gap={'12px'}>
          <TwitterM
            interactive
            color="var(--gray-main)"
            width={24}
            height={24}
            style={{'--color-interactive_pdrly': 'var(--main-blue)'}}
            onClick={twitterHandler}
          />
          <FavoriteFilledM
            interactive
            color="var(--gray-main)"
            width={24}
            height={24}
            style={{'--color-interactive_pdrly': 'var(--main-blue)'}}
            onClick={rateHandler}
          />
          <FacebookM
            interactive
            color="var(--gray-main)"
            width={24}
            height={24}
            style={{'--color-interactive_pdrly': 'var(--main-blue)'}}
            onClick={facebookHandler}
          />
        </Flex>

        <Flex mt={4}>
          <Text
            fontWeight={400}
            fontSize={'12px'}
            lineHeight="16px"
            color="var(--gray-secondary)"
          >
            {chrome.i18n.getMessage('version')} 2.3
          </Text>
        </Flex>
        <Flex mt={4}>
          <Link color="var(--gray-600)" onClick={privacyHandler}>
            <Text fontSize={'14px'} lineHeight="18px" fontWeight={400}>
              {chrome.i18n.getMessage('privacy_policy')}
            </Text>
          </Link>
        </Flex>
        <Flex mt={4}>
          <Link color="var(--gray-600)" onClick={termsHandler}>
            <Text fontSize={'14px'} lineHeight="18px" fontWeight={400}>
              {chrome.i18n.getMessage('terms_of_use')}
            </Text>
          </Link>
        </Flex>
      </Flex>
    </Col>
  );
};
