import './App.css';
import {Route, Routes} from 'react-router-dom';
import {MainScreen} from './pages/MainScreen';
import {DonationScreen} from './pages/DonationScreen';
import {SettingsBreakpoints} from './pages/SettingsBreakpoint';
import {OnboardingBreakpoint} from './pages/OnbardingBreakpoint';
import { ReportIssueScreen } from './pages/ReportIssueScreen';
import { FeedbackScreen } from './pages/FeedbackScreen';
import { UninstallScreen } from './pages/UninstallScreen';
import React from 'react';
import { DebugPage } from './pages/DebugPage';

function App() {
  return (
    <Routes>
      <Route path="/" element={<MainScreen />}></Route>
      <Route path="/donation" element={<DonationScreen />}></Route>
      <Route path="/report" element={<ReportIssueScreen />} />
      <Route path="/feedback" element={<FeedbackScreen />} />
      <Route path="/new-onboarding" element={<OnboardingBreakpoint />}></Route>
      <Route
        path="/settings/:specificRoute"
        element={<SettingsBreakpoints />}
      ></Route>
      <Route path="/uninstall" element={<UninstallScreen />} />
      <Route path="/debug" element={<DebugPage />} />
      <Route path="*" element={<MainScreen />}></Route>
    </Routes>
  );
}

export default App;