import {Flex, Box} from '@semcore/ui/flex-box';
import {Col, Row} from '@semcore/ui/grid';

import {StandsLogo} from '../assets/images';

import {SettingsSidebar} from '../components/SettingsSidebar/SettingsSidebar';

import {BlockingSettings} from './BlockingSettings';
import {FairnessSettings} from './FairnessSettings';
import {BlockingStatistics} from './BlockingStatistics';
import {WhitelistedSitesSettings} from './WhitelistedSitesSettings';
import {ThemesSettingsScreen} from './ThemesSettings';
import useSettingsStore, {sideBarTypes} from '../stores/settingsStore';
import {useContext, useMemo} from 'react';
import {PopupAllowedSitesSettings} from './PopupAllowedSitesSettings';
import Breakpoints from '@semcore/ui/breakpoints';

import HamburgerM from '@semcore/icon/Hamburger/m/index';
import {SettingsPanelSidebar} from '../components/SettingsSidebar/SettingsPanelSidebar';
import {DonationSettings} from './DonationSettings';
import React from 'react';

export const SettingsScreen = () => {
  const index = useContext(Breakpoints.Context);
  const [activeSettingsSection, changeSidePanelVisibility] = useSettingsStore(
    (state) => [state.activeSettingsSection, state.changeSidePanelVisibility]
  );

  const openSidePanel = () => {
    changeSidePanelVisibility(true);
  };

  const currentSettingsSection = useMemo(() => {
    switch (activeSettingsSection) {
      case sideBarTypes.blockingSettings:
        return <BlockingSettings />;
      case sideBarTypes.fairnessSettings:
        return <FairnessSettings />;
      case sideBarTypes.statistics:
        return <BlockingStatistics />;
      case sideBarTypes.whitelistedSites:
        return <WhitelistedSitesSettings />;
      case sideBarTypes.popupAllowedSites:
        return <PopupAllowedSitesSettings />;
      case sideBarTypes.themes:
        return <ThemesSettingsScreen />;
      // case sideBarTypes.donate:
      //   return <DonationSettings />;
    }
  }, [activeSettingsSection]);

  return (
    <Row className="fullscreen-page">
      {index === 0 && <SettingsSidebar />}
      <Col span={index === 0 ? 7 : 10}>
        {index === 1 && (
          <Flex ml={5} mt={5}>
            <HamburgerM
              interactive
              mr={3}
              height={24}
              width={24}
              onClick={openSidePanel}
            />
            <StandsLogo width={105} height={31} />
            <SettingsPanelSidebar />
          </Flex>
        )}
        <Box
          mt={index === 1 ? '28px' : '95px'}
          ml={index === 1 ? 5 : '50px'}
          w={'100%'}
          wMin="280px"
          wMax={activeSettingsSection !== sideBarTypes.statistics ? '570px' : '100%'}
        >
          {currentSettingsSection}
        </Box>
      </Col>
    </Row>
  );
};
