import {Box} from '@semcore/ui/flex-box';
import {Text} from '@semcore/ui/typography';
import {SwitchItemType} from '../../types/entities';
import {SwitchItem} from '../SwitchItem.tsx/SwitchItem';
import { useEffect, useState } from 'react';
import React from 'react';

interface Props {
  /**title for block */
  blockTitle: string;
  /** list of switch items */
  optionsList: SwitchItemType[];
  /** amount of margin (*4px) between each of the switch items in the optionsList */
  optionsListMarginTop?: number;
}

export const BlockingOptionsSection: React.FC<Props> = ({
  blockTitle,
  optionsList,
  optionsListMarginTop,
}) => {
  const [userSettings, setUserSettings] = useState<Record<string, any>>({});

  const toggleSetting = (settingName: string, value: any) => {
    const newSetting = {} as Record<string, any>;
    newSetting[settingName] = value;
    // @ts-ignore
    userSettingsService.update(newSetting)
  }

  useEffect(() => {
    // @ts-ignore
    userSettingsService.register(function(settings) {
      setUserSettings(settings)
    })
  }, [])
  
  return (
    <Box mt={3}>
      <Text bold fontSize={'18px'} lineHeight={'24px'}>
        {blockTitle}
      </Text>
      {optionsList.map((switchItemInfo) => {
        const {text, toggleSettingName, tooltipText} = switchItemInfo;
        return (
        <Box mt={optionsListMarginTop || 3}>
          <SwitchItem
            text={text}
            tooltipText={tooltipText}
            value={userSettings[toggleSettingName]}
            listener={(value:any) => toggleSetting(toggleSettingName, value)}
          />
        </Box>
      )})}
    </Box>
  );
};
