import {create} from 'zustand';

export enum sideBarTypes {
  blockingSettings = 'blockingSettings',
  fairnessSettings = 'fairnessSettings',
  accountDetails = 'accountDetails',
  statistics = 'statistics',
  whitelistedSites = 'whitelistedSites',
  popupAllowedSites = 'popupAllowedSites',
  themes = 'themes',
  donate = 'donate',
}

interface settingsStore {
  activeSettingsSection: sideBarTypes;
  setActiveSettingsSection: (chosenSection: sideBarTypes) => void;

  sidePanelVisible: boolean;
  changeSidePanelVisibility: (visibility: boolean) => void;
}

const useSettingsStore = create<settingsStore>()((set) => ({
  activeSettingsSection: sideBarTypes.blockingSettings,
  sidePanelVisible: false,

  changeSidePanelVisibility: (visibility: boolean) =>
    set((state) => ({
      sidePanelVisible: visibility,
    })),

  setActiveSettingsSection: (chosenSection: sideBarTypes) =>
    set((state) => ({
      activeSettingsSection: chosenSection,
    })),
}));

export default useSettingsStore;
